#wnpApp {
  background: linear-gradient(0deg, #010c12 0%, #033536 50%, #28434a 100%);
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
}

#wnpApp .text-secondary {
  color: #b3bdba !important;
}

#wnpApp .wnpContainer {
  color: #fff;
  grid-template: "wnpAlbumArt wnpMediaInfo" 1fr
                 "wnpFooter wnpFooter" 110px
                 / 1fr 1fr;
  grid-auto-flow: row;
  gap: 0;
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#wnpApp .wnpAlbumArt {
  text-align: center;
  grid-area: wnpAlbumArt;
  padding: 30px;
  overflow: hidden;
}

#wnpApp .wnpMediaInfo {
  grid-area: wnpMediaInfo;
  padding: 30px 30px 30px 0;
}

#wnpApp .wnpFooter {
  grid-area: wnpFooter;
  padding: 0 30px;
}

@media screen and (width <= 500px) {
  #wnpApp .wnpContainer {
    grid-template: "wnpAlbumArt" 2fr
                   "wnpMediaInfo" 1fr
                   "wnpFooter" 140px
                   / 1fr;
    gap: 0;
    display: grid;
  }

  #wnpApp .wnpMediaInfo {
    padding: 0 30px;
  }
}

#wnpApp #wnpBgAlbumArt {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#wnpApp #wnpBgAlbumArt #bgAlbumArtBlur {
  filter: blur(12px) brightness(.6) opacity(.6);
  background-image: url("fake-album-1.69685f7b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  transform: scale(1.25);
}

#wnpApp .wnpAlbumArt #albumArt {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  box-shadow: 3px 3px 16px #000000bf;
}

#wnpApp .wnpMediaInfo {
  text-shadow: 3px 3px 8px #000000bf;
}

#wnpApp .wnpMediaInfo #mediaSource {
  text-align: center;
}

#wnpApp .wnpMediaInfo #mediaSource img {
  filter: invert();
  max-height: 16px;
  margin-bottom: 15px;
}

#wnpApp .wnpMediaInfo #mediaAlbumIdent.badge-outlined, #wnpApp .wnpMediaInfo #mediaQualityIdent.badge-outlined {
  font-weight: var(--bs-body-font-weight);
  border: 1px solid #767d7b;
  margin-right: 10px;
}

#wnpApp .wnpFooter #progressPercent {
  --bs-bg-opacity: .5;
  height: 2px;
}

#wnpApp .wnpFooter .btn-group.primary {
  margin-top: 5px;
}

#wnpApp .wnpFooter .btn-group.primary button {
  border: none;
}

#wnpApp .wnpFooter .btn-group.primary button:hover, #wnpApp .wnpFooter .btn-group.primary button:active {
  color: var(--bs-btn-color);
  background-color: #0000;
}

#wnpApp .wnpFooter .btn-group.primary #btnPlay {
  font-size: 2.25rem;
}

#wnpApp .wnpFooter .btn-group.primary #btnPrev, #wnpApp .wnpFooter .btn-group.primary #btnNext {
  font-size: 1.75rem;
}

#wnpApp .wnpFooter .btn-group.secondary {
  position: absolute;
  top: 5px;
  right: 15px;
}

#wnpApp .wnpFooter .btn-group.secondary button {
  border: none;
  padding: 6px;
}

#wnpApp .wnpFooter .btn-group.secondary button:hover, #wnpApp .wnpFooter .btn-group.secondary button:active {
  color: var(--bs-btn-color);
  background-color: #0000;
}

#wnpApp .wnpFooter .btn-group.secondary #wnpFullscreen {
  display: block;
}

#wnpApp .wnpFooter .btn-group.secondary #wnpFullscreenExit {
  display: none;
}

#wnpApp .wnpFooter #devNameHolder {
  position: absolute;
  bottom: 15px;
  left: 30px;
}

#wnpApp .wnpFooter #devVolumeHolder {
  text-align: right;
  position: absolute;
  bottom: 15px;
  right: 30px;
}

#wnpApp.fullscreen {
  font-size: 1.5rem;
}

#wnpApp.fullscreen h2 {
  font-size: 3rem;
}

#wnpApp.fullscreen .wnpContainer {
  grid-template-rows: 1fr 120px;
  grid-template-columns: 1.2fr .8fr;
}

#wnpApp.fullscreen .wnpAlbumArt {
  padding: 45px;
}

#wnpApp.fullscreen .wnpMediaInfo {
  padding: 45px 45px 45px 0;
}

#wnpApp.fullscreen .wnpFooter {
  padding: 0 45px;
}

#wnpApp.fullscreen .wnpAlbumArt #albumArt {
  object-fit: fill;
  width: 100%;
}

#wnpApp.fullscreen .wnpMediaInfo #mediaSource img {
  max-height: 48px;
  margin-bottom: 30px;
}

#wnpApp.fullscreen .wnpFooter #progressPercent {
  height: 5px;
}

#wnpApp.fullscreen .wnpFooter .btn-group.secondary #wnpFullscreen {
  display: none;
}

#wnpApp.fullscreen .wnpFooter .btn-group.secondary #wnpFullscreenExit {
  display: block;
}

#wnpApp.fullscreen .wnpFooter #devNameHolder {
  bottom: 20px;
  left: 45px;
}

#wnpApp.fullscreen .wnpFooter #devVolumeHolder {
  bottom: 20px;
  right: 45px;
}

